<h1 id="scaling-databases" tabindex="-1"><a class="header-anchor" href="#scaling-databases" aria-hidden="true">#</a> Scaling Databases</h1>
<p>So, you have managed to scale your web application to handle a huge amount of clients, but the web application in turn makes use of your database. What if the database now becomes the bottleneck instead? Then we need to scale that one too! But that's easier said than done.</p>
